body {
    background-color: #424146;
}

.jumbotron {
    background-color: #2a292e;
    color: white;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    margin-bottom: 3rem;
    display: flex;
    flex-direction: row;
}

.logo {
    height: calc(50% + 1vw + 1vh);
}

.title {
    font-size: calc(300% + 1vw + 1vh);
    text-align: center;
    font-family: 'Patrick Hand';
}

.description {
    font-size: calc(80% + 1vw + 1vh);
    text-align: center;
    font-family: 'Patrick Hand';
    color: gray;
}

.characters {
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    flex-wrap: wrap;
}

.control-button {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
    margin-bottom: 2rem;
}

.character {
    /*border-top-right-radius: 15%;*/
    border-top-left-radius: 15%;
    border-bottom-right-radius: 15%;
    font-family: 'Patrick Hand';
    background-color: #201E26;
    color: white;
    margin: auto 0 2rem 0;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.character:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.character img {
    border-top-left-radius: 15%;
    /*border-top-right-radius: 15%;*/
    border-bottom-left-radius: 0%;
    border-bottom-right-radius: 0%;
}

.character-name {
    margin-top: 1rem;
    text-align: center;
    font-size: 2rem;
    opacity: 0.9;
    font-weight: bold;
}

.character-name-Dead:hover {
    opacity: 1;
    color: rgb(202, 55, 55);
}

.character-description-Dead p:hover {
    opacity: 1;
    color: rgb(233, 184, 184);
}

.character-description-Alive p:hover {
    opacity: 1;
    color: rgb(200, 233, 184);
}

.character-name-Alive:hover {
    opacity: 1;
    color: yellowgreen;
}

.image-Dead {
    filter: gray; /* IE6-9 */
    -webkit-filter: grayscale(1); /* Google Chrome, Safari 6+ & Opera 15+ */
    filter: grayscale(1);
}

.overlay {
    opacity: 0.9;
}

.overlay:hover {
    opacity: 1;
}

.state-Alive {
    background-color: greenyellow;
    height: 0.5rem;
    margin-bottom: 1rem;
}

.state-Dead {
    background-color: red;
    height: 0.5rem; 
    margin-bottom: 1rem; 
}

.state-unknown {
    background-color: gray;
    height: 0.5rem;  
    margin-bottom: 1rem;
}

.character-description > p {
    text-align: left;
    font-size: 1.2rem;
    margin-top: 0rem;
    padding-left: 1rem;
}

i {
    font-size: 1rem;
    color: inherit;
    margin-right: 1rem;
    text-align: left;
}

.character a {
    color: inherit;
}

.character a:hover {
    color: inherit;
    text-decoration:none;
}

.tooltip {
    font-size: 1rem;
}

.character-name:hover .tooltip {
    visibility: visible;
}

.btn-dark {
    width: 150px;
}

.btn-dark:hover {
    background-color: rgb(115, 156, 33);
}

.error-container {
    flex-direction: column;
}

.error-text {
    font-size: calc(150% + 1vw + 1vh);
    text-align: center;
    font-family: 'Patrick Hand';
    color: white;    
}

.error-img {
    width: 100%;
    padding: -5rem;
}